@import url(https://fonts.googleapis.com/css2?family=Questrial&display=swap);
* {
  box-sizing: border-box;
  padding: 0px;
  margin: 0px;
  font-family: "Questrial", sans-serif;
  background: #4b4b4b;
}

.button {
  /* dimensions */
  width: 250px;
  height: 70px;
  border-radius: 9px;
  margin-bottom: 20px;

  /* colouring */
  background: linear-gradient(
    89.09deg,
    #17468c 3.33%,
    #2992b3 53.38%,
    #71c2fc 81.81%
  );

  /* arrangements */
  display: flex;
  justify-content: center;
  align-items: center;

  /* transitions*/
  transition: width 0.4s ease-out 0s, height 0.4s ease-out 0s;
}

.button_text {
  font-family: Questrial;
  font-style: normal;
  font-weight: normal;
  font-size: 27px;
  background: none;
  color: #ffffff;
}

/* pseudo selector */

.button:hover {
  width: 270px;
  height: 80px;
  cursor: pointer;
}

.button_container {
  /* dimensions */
  width: 100vw;
  height: 100vh;
  margin: 0px;
  padding: 0px;

  /* arrangements */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  /* testing */
}

.modal_container_wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
}
.modal_container {
  /*dimensions */
  width: 311px;
  height: 198px;
  border-radius: 67px;

  /*colouring*/
  border: 3px solid #14a6ca;

  /*padding and margin */
  padding-top: 50px;
  padding-left: 35px;
}

.modal_input {
  outline: none;
  border: none;
  color: white;
  font-size: 20px;
  border-bottom: 2px solid #2992b3;
}

.modal_input::-webkit-input-placeholder {
  color: rgb(230, 222, 222);
  opacity: 0.8;
}

.modal_input:-ms-input-placeholder {
  color: rgb(230, 222, 222);
  opacity: 0.8;
}

.modal_input::-ms-input-placeholder {
  color: rgb(230, 222, 222);
  opacity: 0.8;
}

.modal_input::placeholder {
  color: rgb(230, 222, 222);
  opacity: 0.8;
}

.button_override {
  width: 200px;
  height: 30px;
  margin-top: 20px;
  margin-left: 10px;
  transition: width 0.3s ease-in-out 0s, height 0.3s ease-in-out 0s;
}

.button_override:hover {
  cursor: pointer;
  width: 210px;
  height: 35px;
}

.button_text_override {
  font-size: 15px;
  background: none;
}

.svg-image {
  background: none;
}

.cell {
  /* dimensions*/
  width: 80px;
  height: 80px;
  border-radius: 20px;

  /* alignments */
  display: flex;
  justify-content: center;
  align-items: center;
  /*decoration*/
  background: #2992b3;

  /* transition */
  transition: width 0.2s ease-out 0s, height 0.2s ease-in-out 0s;
}

.cell:hover {
  width: 85px;
  height: 85px;
  cursor: pointer;
}

.row {
  /*dimensions */
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin-bottom: 10px;
}

.player-info {
  margin-top: 10px;
  width: 100%;
  display: flex;
  justify-content: space-around;
  color: white;
}

.player-active {
  -webkit-animation: showActive 2s infinite;
          animation: showActive 2s infinite;
}

.show-info {
  color: white;
  -webkit-animation: showInfo 3s infinite;
          animation: showInfo 3s infinite;
}

@-webkit-keyframes showActive {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0px);
  }
}

@keyframes showActive {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0px);
  }
}

@-webkit-keyframes showInfo {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(0px);
  }
}

@keyframes showInfo {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(0px);
  }
}

.board {
  width: 300px;
  height: 300px;
  margin: 60px auto;
  padding: 0px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.board-heading {
  color: white;
  text-align: center;
  margin: 20px 0px;
}



.notification-container {
  position: absolute;
  bottom: 0;
  left: -50%;

  width: 200px;
  height: 30px;
  padding-top: 5px;

  background-color: rgb(124, 110, 110);
  -webkit-animation: showNotification 3s none;
          animation: showNotification 3s none;
}

.notification-message {
  width: 90%;
  height: 80%;
  margin: auto;
  font-size: 15px;
  font-family: Questrial;
  color: white;
  background: none;
}
.success {
  width: 100%;
  height: 20%;
  background-color: rgb(51, 144, 51);
}

.error {
  width: 100%;
  height: 20%;
  background-color: rgb(204, 24, 24);
}

.info {
  width: 100%;
  height: 20%;
  background-color: rgb(198, 218, 24);
}
@-webkit-keyframes showNotification {
  0% {
    left: -50%;
  }
  50% {
    left: 0;
  }
  100% {
    left: 0;
  }
}
@keyframes showNotification {
  0% {
    left: -50%;
  }
  50% {
    left: 0;
  }
  100% {
    left: 0;
  }
}

